
body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #2b2b2b; 
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: #e0e0e0; 
}

.App {
  background-color: #3c3c3c; 
  padding: 20px;
  border-radius: 15px; 
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5); 
  width: 350px; 
}

h1 {
  text-align: center;
  color: #ff9800; 
  margin-bottom: 20px; 
}

.input-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px; 
}

input {
  width: calc(70% - 12px); 
  padding: 12px; 
  border: none; 
  border-radius: 8px; 
  outline: none; 
  font-size: 16px; 
  background-color: #4f4f4f; 
  color: #e0e0e0; 
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5); 
}

input::placeholder {
  color: #aaa;
}

button {
  padding: 12px 15px; 
  background-color: #ff9800; 
  color: white;
  border: none;
  border-radius: 8px; 
  cursor: pointer;
  font-size: 16px; 
  transition: background-color 0.3s; 
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5); 
}

button:hover {
  background-color: #ac6700; 
  font-size: 17px; 
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: flex;
  justify-content: space-between;
  padding: 12px; 
  border-bottom: 1px solid #555; 
  color: #e0e0e0; 
}

li div {
  display: flex; 
}

li button {
  background-color: #be4a57; 
  margin-left: 5px; 
  padding: 6px 10px; 
  border-radius: 5px; 
  transition: background-color 0.3s; 
}

li button:hover {
  background-color: #c82333; 
}

li button:nth-child(2) { 
  background-color: #4585ca; 
}

li button:nth-child(2):hover {
  background-color: #0056b3; 
}
